import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';


class Page extends PureComponent{
    render(){
        return (
            <div className="main-container">
                <h1 className="page-h1"></h1>
                <blockquote className="page-tip">
                    <br/>
                </blockquote>

                <h1 className="page-h1"></h1>

                <Row>
                    <Col grid={0.5} className="code-col">
                        <Label className="code-box">
                            <Demo1></Demo1>
                        </Label>
                        <Label className="code-box">
                            <Demo3></Demo3>
                        </Label>
                    </Col>
                    <Col grid={0.5} className="code-col">
                        <Label className="code-box">
                            <Demo2></Demo2>
                        </Label>
                        <Label className="code-box">
                            <Demo4></Demo4>
                        </Label>
                    </Col>
                </Row>

                <h2 className="page-h2">API</h2>
                <h3 className="page-h3">Uploadify</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'buttonText', desc: '', type: 'string', default: 'upload'},
                    {param: 'files', desc: '', type: 'array', default: ''},
                    {param: 'params', desc: '', type: 'object', default: ''},
                    {param: 'mode', desc: 'fallsgrid', type: 'string', default: 'falls'},
                    {param: 'onInit', desc: '', type: 'function', default: ''},
                    {param: 'auto', desc: '', type: 'boolean', default: 'true'},
                    {param: 'onFilesAdded', desc: '', type: 'function', default: ''},
                    {param: 'onProgress', desc: '', type: 'function', default: ''},
                    {param: 'onFileUploaded', desc: '', type: 'function', default: ''},
                    {param: 'onUploadComplete', desc: '', type: 'function', default: ''},
                    {param: 'onException', desc: '', type: 'function', default: ''},
                    {param: 'onRemoveFile', desc: '', type: 'function', default: ''},
                    {param: 'thumbnail', desc: 'falls', type: 'boolean', default: 'false'},
                    {param: 'maxSize', desc: 'bkbmbgb', type: 'string', default: ''},
                    {param: 'mimeTypes', desc: '', type: 'array', default: ''},
                    {param: 'name', desc: '', type: 'string', default: 'file'},
                    {param: 'url', desc: 'url', type: 'string', default: ''},
                    {param: 'multi', desc: '', type: 'boolean', default: 'true'}
                ]}></Table>

                <h3 className="page-h3">Methods</h3>

                <ul className="code-methods">
                    <li>
                        <i>setParams(params)</i>
                        <span>key/value</span>
                        <ul>
                            <li>
                                params {`Object`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>start()</i>
                        <span></span>
                    </li>
                </ul>


                <h3 className="page-h3">mimeTypes</h3>
                <ul className="code-methods">
                    <li>
                        <i>{`[{title : "Image files", extensions : "jpg,gif,png"}]`}</i>
                        <ul>
                            <li>
                                title {`String`} 
                            </li>
                            <li>
                                extensions {`String`} ,
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



